.contact {
    
    .container {
        width: 100%;
        padding-top: 50px;
    
        .inner {
            display: flex;
            width: 1180px;
            margin: 50px auto 100px auto;
    
            .title {
                width: 20%;
                font: 600 30px/1.3 $font1;
                color: $mainColor;
                text-align: right;
            }
        }
    }

    
    
    // Get in touch CSS
    .container.getintouch {
        //Scroll Animation : Start
        &.on .inner {
            .contactForm, .contactDetail {
                opacity: 1;
                transform: translateY(0px);
            }
        }
        .inner {
            .contactForm, .contactDetail {
                opacity: 0;
                transform: translateY(30px);
                transition: 1s;
            }
        }
        //Scroll Animation : End

        .success {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            align-content: flex-start;
            width: 50%;
            padding: 70px;
            text-align: center;
            
            .mailIcon {
                font-size: 70px;
                margin-bottom: 10px;
                color: $mainColor;
            }

            h2 {
                font: bold 30px/1.3 $font1;
                margin-bottom: 40px;
                color: $mainColor;
            }

            div {
                display: flex;
                justify-content: space-around;
                width: 100px;
                color: $mainColor;
                margin-bottom: 40px;
            }

            p {
                font: 18px/1.5 $font1;
                color: $subColor1;
                padding: 30px;
                background: $subColor1;
                color: #fff;
            }
        }

        .contactForm {
            width: 50%;
            padding: 0px 50px 30px;

            h2 {
                text-align: right;

                span {
                    display: inline-block;
                    font: 600 20px/1 $font1;
                    color: #fff;
                    background: $mainColor;
                    padding: 10px;
                }
            }

            fieldset { border: 0; }

            table {
                width: 100%;
                border-collapse: separate;
                border-spacing: 0 20px;
                margin-top: -10px;

                tr {
                    width: 100%;
                    padding: 0px 20px;

                    &.formBtns {
                        th {
                            width: 100%;
                            background: transparent;
                            padding-top: 20px;
                            font-size: 0;

                            input {
                                display: inline-block;
                                width: 150px;
                                height: 45px;
                                margin-right: 10px;
                                font: 600 12px/1 $font1;
                                letter-spacing: 1px;
                                cursor: pointer;
                            }

                            input[type=reset] {
                                border: 1px solid $mainColor;
                                background: #fff;
                                color: $mainColor;
                            }

                            input[type=submit] {
                                border: 0;
                                border: 0;
                                background: $mainColor;
                                color: #fff;
                                transition: .5s;

                                &:hover {
                                    color: $pointColor;
                                }
                            }
                        }
                    }

                    th:not(:last-child) {
                        display: flex;
                        justify-content: space-between;
                        width: 100%;
                        min-width: 120px;
                        height: 20px;
                        text-align: left;
                        
                        font: 600 14px/10px $font1;
                        color: $mainColor;

                        .errMsg {
                            font: bold 12px/1 $font1;
                            color: $pointColor;

                            &::before {
                                content: "*";
                            }
                        }
                    }

                    td {
                        display: block;
                        width: 100%;
                        height: 100%;
                        font-size: 0;

                        input[type=text], input[type=number], input[type=email] {
                            width: 100%;
                            height: 45px;
                            border: 0;
                            padding: 1em;
                            font: 14px/1 $font1;
                            color: #333;
                            outline: none;
                            border: 1px solid $subColor1;
                            border-bottom: 4px solid $subColor1;
                        }

                        textarea {
                            width: 100%;
                            height: 200px;
                            resize: none;
                            border: 0;
                            font: 14px/1 $font1;
                            color: #333;
                            outline: none;
                            padding: 1em;
                            border: 1px solid $subColor1;
                            border-bottom: 4px solid $subColor1;
                        }

                    }
                    
                }
            }
        }

        .contactDetail {
            display: flex;
            flex-wrap: wrap;
            align-items: space-between;
            width: 30%;
            height: 650px;
            padding: 50px;
            border-left: 1px solid #ddd;

            div {

                h3, h4, h5 {
                    font: 600 16px/1 $font1;
                    color: #555;
                    margin-bottom: 10px;
                }

                p {
                    font: normal 14px/1.3 $font2;
                    color: #666;
                    margin-bottom: 10px;
                }
                
                a {
                    font: 500 14px/1 $font1;
                    color: #555;

                    &:hover {
                        color: $pointColor;
                    }

                    i {
                        display: inline-flex;
                        justify-content: center;
                        align-items: center;
                        width: 20px;
                        height: 20px;
                        border-radius: 50%;
                        background: $pointColor;
                        color: #fff;
                        font-size: 10px;
                        margin-right: 3px;
                    }
                }
            }
        }
    }

    .container.location {
        margin-bottom: 150px;

        //Scroll Animation : Start
        &.on .inner .location {
            opacity: 1;
            transform: translateY(0px);
        }
        .inner .location {
            opacity: 0;
            transform: translateY(30px);
            transition: 1s;
        }
        //Scroll Animation : End

        .location {
            position: relative;
            width: 100%;
            height: 444px;
            margin: 0 50px;
    
            h1 {
                font: 600 24px/1 $font1;
    
                span {
                    display: inline-block;
                    padding: 10px;
                    color: #fff;
                    background: $mainColor;
                }
            }
    
            #map {
                width: 70%;
                height: calc(100% - 44px);
                filter: grayscale(.7);
                transition: .5s;
                float: left;
    
                &:hover {
                    filter: grayscale(0);
                }
            }
    
            .branchBtns {
                position: absolute;
                bottom: 0px;
                right: calc(30% + 10px);
                display: flex;
                flex-direction: column;
                width: 120px;
    
                li {
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    width: 120px;
                    height: 50px;
                    margin-bottom: 10px;
                    font: 600 12px/1 $font1;
                    border: 1px solid $mainColor;
                    background: #fff;
                    color: $mainColor;
                    cursor: pointer;
                    transition: .5s;
    
                    span {
                        display: block;
                        margin-top: 5px;
                        font: 600 10px/1 $font1;
                    }
    
                    &.on, &:hover {
                        background: $mainColor; 
                        color: #fff; 
                        span { color: $pointColor; }
                    }
                }
            }
    
            .address {
                width: 28%;
                margin-left: 2%;
                height: calc(100% - 44px);
                float: left;
                text-align: center;
                border: 2px solid #eee;
    
                .officeTitle {
                    width: 100%;
                    padding: 35px 20px;
                    background: #eee;
    
                    h2 {
                        font: bold 20px/1 $font1;
                        color: $mainColor;
                        margin-bottom: 10px;
                        letter-spacing: 1px;
                    }
    
                    h3 {
                        font: 600 14px/1 $font1;
                        color: $mainColor;
                    }
    
                }
    
                .addressDetail {
                    width: 100%;
                    height: calc(100% - 114px);
                    padding: 45px 30px;
    
                    h4 {
                        font: 600 14px/1.5 $font1;
                        color: #333;
                        margin-bottom: 20px;
                        
                        &::after {
                            content: "";
                            display: block;
                            width: 20px;
                            height: 2px;
                            background: $mainColor;
                            margin: 0 auto;
                            margin-top: 15px;
                        }
                    }
    
                    p {
                        font: 600 12px/1.5 $font1;
                        color: #555;
                        margin-bottom: 10px;
    
                        span {
                            color: $pointColor;
                            margin-right: 10px;
                        }
                    }
                }
                }
    
    
            &::after {
                content: "";
                display: block;
                clear: both;
            }
        }
    }
    
    
    
}

//Tablet------------------------------------------------
@media screen and (max-width: $tablet) {
.contact {
    .container {
        .inner {
            flex-wrap: wrap;
            width: 100%;
            margin: 20px auto 0px;
    
            .title {
                width: 100%;
                text-align: center;
                margin-bottom: 30px;
                text-transform: uppercase;
            }
        }
    }
    
    // Get in touch CSS
    .container.getintouch {
        .success {
            width: 60%;
            padding: 5vw;
            
            .mailIcon {
                display: block;
                width: 100%;
            }

            h2 {
                font: bold 24px/1.3 $font1;
            }

            p {
                font: 16px/1.5 $font1;
            }
        }

        .contactForm {
            width: 60%;
            padding: 0px 5vw 30px;

            h2 {
                text-align: left;
            }

            table {
                tr {
                    &.formBtns {
                        th {
                            input {
                                width: 100px;
                                height: 40px;
                            }
                        }
                    }
                }
            }
        }

        .contactDetail {
            width: 40%;
            padding: 5vw;
        }
    }

    .container.location {
        margin-bottom: 100px;

        .location {
            height: auto;
            margin: 0 5vw;
    
            #map {
                width: 100%;
                height: 250px;
            }
    
            .branchBtns {
                top: 59px;
                right: 15px;
            }
    
            .address {
                display: flex;
                width: 100%;
                margin-left: 0%;
                height: 250px;
                border-top: 0px;
    
                .officeTitle {
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: center;
                    align-content: center;
                    width: 30%;
                    padding: 0px;
    
                    h2 {
                        width: 100%;
                    }
    
                    h3 {
                        width: 100%;
                    }
    
                }
    
                .addressDetail {
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: center;
                    align-content: center;
                    width: 70%;
                    height: auto;
                    padding: 0px;
    
                    h4 {
                        width: 100%;
                        font: 600 14px/1.4 $font1;
                        margin-bottom: 15px;
                        
                        &::after {
                            margin-top: 10px;
                        }
                    }
    
                    p {
                        width: 100%;
                        font: 600 12px/1.3 $font1;
                    }
                }
            }
        }
    }
}
}


//Mobile------------------------------------------------
@media screen and (max-width: $mobile) {
.contact {
    
    .container {
        .inner {
            margin: 0px auto;
    
            .title {
                font: 600 26px/1.3 $font1;
                margin-bottom: 20px;
            }
        }
    }
    
    // Get in touch CSS
    .container.getintouch {
        .success {
            width: 100%;
            margin-bottom: 50px;

            h2 {
                font: bold 20px/1.3 $font1;
                margin-bottom: 30px;
            }

            div {
                margin-bottom: 30px;
            }

            p {
                font: 14px/1.5 $font1;
            }
        }

        .contactForm {
            width: 100%;

            h2 {
                text-align: right;

                span {
                    font: 600 16px/1 $font1;
                }
            }

            table {
                tr {
                    &.formBtns {
                        th {
                            padding-top: 0px;
                        }
                    }

                    th:not(:last-child) {
                        .errMsg {
                            font: 600 10px/1 $font1;
                        }
                    }

                    td {
                        input[type=text], input[type=number], input[type=email] {
                            font: 12px/1 $font1;
                        }

                        textarea {
                            font: 12px/1.3 $font1;
                        }

                    }
                    
                }
            }
        }

        .contactDetail {
            width: 100%;
            height: auto;
            margin: 0px;
            padding: 10vw;
            border: 0px;
            background: $subColor1;

            div {
                display: flex;
                flex-wrap: wrap;
                justify-content: center;
                margin-bottom: 30px;

                &:last-child { 
                    margin-bottom: 0px;
                }

                h3, h4, h5 {
                    font: 16px/1 $font1;
                    color: #eee;
                }

                p {
                    color: #ddd;
                    text-align: center;
                }
                
                a {
                    color: #ddd;
                }
            }
        }
    }

    .container.location {
        margin-bottom: 70px;

        .location {
            height: auto;
            margin: 0 3vw;
    
            h1 {
                font: 600 18px/1 $font1;
            }
    
            #map {
                height: 300px;
            }
    
            .branchBtns {
                top: calc(38px + 5px);
                right: 5px;
                width: 110px;
    
                li {
                    width: 110px;
                }
            }
    
            .address {
                border: 1px solid #eee;
    
                .officeTitle {
                    h2 {
                        font: bold 16px/1.3 $font1;
                    }
    
                    h3 {
                        font: 600 12px/1 $font1;
                    }
    
                }
    
                .addressDetail {
                    h4 {
                        font: 600 12px/1.4 $font1;
                    }
                }
            }
        }
    }

}
}